.dialogs {
	display: grid;
	grid-template-columns: 1fr 2fr;
}

.dialogItems {
	border-right: 1px solid #e6ecf5;
}

.dialogItems :hover {
	background-color: #fafbfd;
}

.messages {
	padding: 20px;
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
}

.message {
	max-width: 70%;
	width: fit-content;
	background-color: #f0f4f9;
	border-radius: 5px;
	padding: 5px 25px;
	margin: 5px 0;
}

.mi {
	align-self: flex-end;
	text-align: right;
	color: white;
	background-color: #7c5ac2;
}

.messages :first-child {
	margin-top: 0;
}

.messages :last-child {
	margin-bottom: 0;
}

.dialog {
	display: flex;
	align-items: center;
	font-weight: 700;
	padding: 20px 30px;
	border-bottom: 1px solid #e6ecf5;
}

.dialog a {
	color: #888da8;
	font-weight: 700;
	font-size: 14px;
	text-decoration: none;
}

.dialogItems :last-child {
	border-bottom: none;
}

.active {
	background-color: #fafbfd;
	color: #515365;
}

.avatar {
	position: relative;
	flex: 0 0 45px;
	height: 45px;
	border-radius: 50%;
	overflow: hidden;
	margin-right: 20px;
}

.avatar img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.messageAdd {
	margin-top: 30px;
	width: 100%;
	display: flex;
	flex-direction: column;
}

.messageAdd textarea {
	display: block;
	outline: none;
	-moz-appearance: none;
	min-height: 50px;
	margin: 0;
	resize: none;
	overflow: hidden;
	border: 1px solid #e6ecf5;
	padding: 10px;
	font-family: Roboto;
	color: #515365;
	border-radius: 5px;
	transition: all ease-in-out 0.3s;
}

.messageAdd textarea:focus {
	border: 1px solid #ff5e3a;
}

.messageAdd :last-child {
	margin-top: 5px;
	align-self: flex-end;
}
